<template>
  <el-card class="forCard">
    <el-form :form="formData" ref="form">
      <el-form-item label="Type" prop="type">
        <el-select v-model="formData.type" placeholder="Chose type">
          <el-option label="Income" value="INCOME"/>
          <el-option label="Outcome" value="OUTCOME"/>
        </el-select>
      </el-form-item>
      <el-form-item label="Comments" prop="comments">
        <el-input v-model="formData.comments"></el-input>
      </el-form-item>
      <el-form-item label="Value" prop="value">
        <el-input v-model.number="formData.value"></el-input>
      </el-form-item>
      <el-button type="primary" @click="onSubmits">Submit</el-button>
    </el-form>
  </el-card>
</template>

<script>
export default {
  name: "Form",
  data:() => ({
    formData: {
      type: "INCOME",
      comments: '',
      value: 0,
    }
}),
  methods: {
    onSubmits(){
      if(!this.formData.value){
        alert('Значение не может быть равно 0!');
        return
      }
      this.$emit('submitForm', {...this.formData});
      //this.$refs.addItemForm.resetFields();
    }
  }
}
</script>

<style scoped>
.forCard {
  max-width: 500px;
  margin: 30px auto;
}
</style>