<template>
  <div class="total-value" :class="{'green-total': total > 0, 'black-total': total === 0, 'red-total': total < 0}">
    Total: {{ total }}</div>
</template>

<script>
export default {
name: "TotalBalance",
  props: {
    total: {
      type: Number,
      default: 0
    },
    class: {
      type: String,
      default: 'black'
    }
  }
}
</script>

<style scoped>
  .total-value{
    font-size: 26px;
    text-transform: uppercase;
    padding: 20px;
    text-align: center;
  }
  
  .green-total{
    color: green;
  }
  .black-total{
    color: black;
  }
  .red-total{
    color: red;
  }
</style>