<template>
  <div id="app">
    <TotalBalance :total="totalBalance" />
    <Form @submitForm="submitForm"/>
    <BudgetList :list="list" @deleteItem="delItem"/>
  </div>
</template>

<script>
import BudgetList from './components/BudgetList';
import TotalBalance from "./components/TotalBalance";
import Form from "./components/Form";

export default {
  name: 'App',
  components: {
    TotalBalance,
    BudgetList,
    Form
  },
  data: () => ({
    list: {
      1: {
        type: 'INCOME',
        value: 100,
        comments: 'lul',
        id: 1,
        budgetClass: 'greenItem',
      },
      2: {
        type: 'INCOME',
        value: -50,
        comments: 'lul',
        id: 2,
        budgetClass: 'redItem',
      }
    },
    class: 'black'
  }),
  computed: {
    totalBalance(){
      return Object.values(this.list).reduce(
          (acc, item) => acc + item.value, 0)
    },
  },
  methods: {
    delItem(id){
      this.$delete(this.list, id)
    },
    submitForm(data){
      if(data.type === 'OUTCOME'){
        data.value = Math.abs(data.value) * (-1);
        data.budgetClass= 'redItem';
      } else{
        data.value = Math.abs(data.value);
        data.budgetClass= 'greenItem'
      }
      const newObject = {
        ...data,
        id: String(Math.random())
      }
      console.log(newObject)
      this.$set(this.list, newObject.id, newObject);
    }
  }
}
</script>

