<template>
  <div class="budget-list-wrap">
    <el-card :header="header">
      <template v-if="isEmpty">
          <div class="list-item" v-for="(item, props) in list" :class="item.budgetClass" :key="props">
          <span class="budget-comment">{{ item.comments }}</span>
          <span class="budget-value">{{ item.value }}</span>
          <el-button type="danger" size="min" @click="deleteItem(item.id)">Delete</el-button>
        </div>
      </template>
      <el-alert v-else type="info" :title="emptyTitle"/>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "BudgetList",
  props: {
    list: {
      type: Object,
      default: () => ({})
    },
  },
  data: () => ({
    header: 'Budget list',
    emptyTitle: "Empty List"
  }),
  methods: {
    deleteItem(id) {
      this.$emit("deleteItem", id);
    },
  },
  computed: {
    isEmpty(){
      return Boolean(Object.keys(this.list).length);
    }
  }
}
</script>

<style scoped>
.budget-list-wrap{
  max-width: 500px;
  margin: auto;
}
.list-item{
  display: flex;
  align-items: center;
  padding: 10px 15px;
  border-radius: 5px;
  margin-top: 10px;
}
.budget-value{
  font-weight: bold;
  margin-left: auto;
  margin-right: 20px;
}
.budget-comment{
  max-width: 450px;
  white-space: pre-line;
  display: block;
}
.redItem{
  background-color: rgba(255,0,14,0.8);
}
.greenItem{
  background-color: rgba(14,255,38,0.8);
}
</style>